import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { ToastController, LoadingController, AlertController } from '@ionic/angular';
import { AccessProviders } from '../../providers/access-providers';
import { HttpClient } from '@angular/common/http';
import { from, Observable, Subscriber } from 'rxjs';

// datos de pedido diario 
import { registroPDA } from '../modelo_registroPDA/registroPDA';
import { Validators, FormControl,  FormGroup } from '@angular/forms';


//productos
import { productoa } from '../modelo_producto/modelo_productoa';



@Component({
  selector: 'app-registro-pedi-dia',
  templateUrl: './registro-pedi-dia.page.html',
  styleUrls: ['./registro-pedi-dia.page.scss'],
})
export class RegistroPediDiaPage implements OnInit {


  //datos de pedido darios 

  objeto:registroPDA = { Des_de_alimnentos:"", Unidad_de_Media:"", Gr_ML_Desayuno:"", Gr_ML_Sopa:"", Gr_ML_Segundo:"", Gr_ML_Cena:""}
  
  pruebas: registroPDA[] = [];
  url = 'http://localhost/proG/server_api/crear_registroPDA.php';
  url2 = 'http://localhost/proG/server_api/menu_registroPDA.php';
  id: number;
  disabledButton;

  
  //producto

  objeto1:productoa = { tipo:"", nombreali:"", cantidad_pro:"", cantidad_min:"", cantidad_med:"", cantidad_max:""}

  pruebas1: productoa[] = [];
  url3 = 'http://localhost/proG/server_api/menu_productoa.php';



  constructor(
    public http: HttpClient,
    private router: Router,
    private toastCtrl: ToastController,
    private loadingCtrl: LoadingController,
    private alertCtrl: AlertController,
    private accsPrvds: AccessProviders,
    private actRoute: ActivatedRoute

  ) {
    this.listarRegistroPDA();
    this.listarRegistroPA();
   }

 
  ngOnInit() {
    
  }

  ionViewDiEnter(){
    this.disabledButton = false;
  }


  //////////////////////////sdsd/////////

  agregarRegistroPDA(obj :registroPDA): Observable<registroPDA>{
    return this.http.post<registroPDA>(this.url, JSON.stringify(obj))
  }

  async agregarNU() {
    this.agregarRegistroPDA(this.objeto)
    .subscribe((resp) =>{
      this.objeto.Des_de_alimnentos='';
      this.objeto.Unidad_de_Media='';
      this.objeto.Gr_ML_Desayuno='';
      this.objeto.Gr_ML_Sopa='';
      this.objeto.Gr_ML_Segundo='';
      this.objeto.Gr_ML_Cena='';
      this.listarRegistroPDA();
      console.log(resp)
    })
    const toast = await this.toastCtrl.create({
      message: "Registro Exitoso",
      duration: 1500
    });
    toast.present();
  }

  async delData(a){
    return new Promise(resolve => {
      let body = {
        aksi: 'eliminar_PDA',
        id: a
      }
      this.accsPrvds.postData(body, 'proses_api.php').subscribe((res:any)=>{
        if(res.success==true){
          this.presentToast('Eliminado con exito');
          this.listarRegistroPDA();
        }else{
          this.presentToast('delete error');
        }
      });

    }); 

  }

  async presentToast(a){
    const toast = await this.toastCtrl.create({
      message: a,
      duration: 1500
    });
    toast.present();
  }

  getRegistroPDA(): Observable<any>{
    return this.http.get(this.url2);
  }

  listarRegistroPDA(){
    this.pruebas = [];
    this.getRegistroPDA().subscribe(res => {
      for(let i=0; i<res.length; i++){
        this.pruebas.push(res[i]);
      }
    })
  }


  abrirCrud(a){
    this.router.navigate(['/mod-pda/'+a]);
  }


  //producto /////////////////

  getRegistroPA(): Observable<any>{
    return this.http.get(this.url3);
  }

  listarRegistroPA(){
    this.pruebas1 = [];
    this.getRegistroPA().subscribe(res => {
      for(let i=0; i<res.length; i++){
        this.pruebas1.push(res[i]);
      }
    })
  }


}
